
import { Component, Vue } from "vue-property-decorator";
import { State, Getter, Action, Mutation, namespace } from "vuex-class";

@Component
export default class extends Vue {
  created() {}
  @Getter("words") words: any;
  @State("dictionary") dictionary: any;

  get longInfoList() {
    return [
      {
        icon: ["fas", "home"],
        msg: this.words.address,
      },
    ];
  }

  get shortInfoList() {
    return [
      {
        icon: ["fas", "envelope"],
        msg: this.words.email,
      },
      {
        icon: ["fas", "phone-alt"],
        msg: this.words.phone,
      },
      {
        icon: ["fas", "tv"],
        msg: this.words.web,
      },
    ];
  }

  setColor(item: any) {
    item.hovTheme = item.theme;
  }

  resetColor(item: any) {
    item.hovTheme = "#000";
    item.bgC = "#fff";
  }

  icons = [
    {
      hovTheme: "#000",
      icon: ["fab", "github"],
      href: "https://github.com/lin-nie",
      theme: "rgb(255, 0, 0)",
    },
    {
      hovTheme: "#000",
      icon: ["fab", "google"],
      href: "https://scholar.google.com/citations?user=QNRj_g8AAAAJ&hl=en",
      theme: "rgb(67, 135, 246)",
    },
    {
      hovTheme: "#000",
      icon: ["fab", "linkedin"],
      href: "https://www.linkedin.com/in/nie-lin/",
      theme: "rgb(0, 127, 178)",
    },
    {
      hovTheme: "#000",
      icon: ["fab", "twitter-square"],
      href: "https://twitter.com/NieLin6",
      theme: "rgb(29, 155, 240)",
    },
  ];
}
